@tailwind base;

/* Globals ------------------------------------------------------------------ */
:root {
  --f-serif           : 'Lexend Deca';
  --f-sans            : 'Open Sans';
  --f-mono            : 'Victor Mono';

  --c-1               : #FF9AA2;
  --c-2               : #FFB7B2;
  --c-3               : #FFDAC1;
  --c-4               : #B5EAD7;
  --c-5               : #C7CEEA;

  --c-w               : #F1F3F4;
  --c-g1              : #DEE1E6;
  --c-g2              : #BABCBE;
  --c-g3              : #535353;
  --c-b               : #202124;

  --bg                : var(--c-b);
  --fg                : var(--c-w);

  --measure           : 120ch;

  --ratio             : 1.45;
  --s-5               : calc(var(--s-4) / var(--ratio));
  --s-4               : calc(var(--s-3) / var(--ratio));
  --s-3               : calc(var(--s-2) / var(--ratio));
  --s-2               : calc(var(--s-1) / var(--ratio));
  --s-1               : calc(var(--s0)  / var(--ratio));
  --s0                : 0.75rem;
  --s1                : calc(var(--s0)  * var(--ratio));
  --s2                : calc(var(--s1)  * var(--ratio));
  --s3                : calc(var(--s2)  * var(--ratio));
  --s4                : calc(var(--s3)  * var(--ratio));
  --s5                : calc(var(--s4)  * var(--ratio));

  background-color    : var(--bg);
  color               : var(--fg);
  font-family         : var(--f-sans);
}

@media only screen 
and (min-device-width : 375px) 
and (max-device-width : 812px) {
  :root {
    --s0              : 0.45rem;
  }
}

* {
  box-sizing          : border-box;
  max-width           : var(--measure);
  transition          : all 0.2s ease;
}


/* Elements ----------------------------------------------------------------- */
html,
body {
  background-color    : var(--bg);
  color               : var(--fg);
  margin              : 0;
  padding             : 0;
}


html,
body,
div,
header,
nav,
main,
footer {
  max-width           : none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family         : var(--f-serif);
}

h1 {
  font-size           : var(--s5);
}

h2 {
  font-size           : var(--s4);
}

h3 {
  font-size           : var(--s3);
}

img {
  max-width           : 100%;
}

code,
pre {
  font-family         : var(--f-mono);
}

/* Layouts ------------------------------------------------------------------ */
/* * Stack ------------------------------------------------------------------ */
.stack {
  --space             : 1.5rem;

  display             : flex;
  flex-direction      : column;
  justify-content     : flex-start;
}

.stack > * {
  margin-bottom       : 0;
  margin-top          : 0;
} 

.stack > * + * {
  margin-top          : var(--space);
}

/* * Box -------------------------------------------------------------------- */
.box {
  background-color    : var(--c-w);
  border              : var(--border-thin) solid;
  color               : var(--c-b);
  padding             : var(--s5);
}

.box * {
  color               : inherit;
}

.box.invert {
  color               : var(--c-w);
  background-color    : var(--c-b);
}

/* * Centre ----------------------------------------------------------------- */
.centre {
  box-sizing          : content-box;
  margin-left         : auto;
  margin-right        : auto;
  max-width           : var(--measure);
  /* display             : flex; */
  /* flex-direction      : column; */
  /* align-items         : center; */
}
  
/* * Cluster ---------------------------------------------------------------- */
.cluster {
  --space             : 1rem;
  overflow            : hidden;
}

.cluster > * {
  display             : flex;
  flex-wrap           : wrap;
  justify-content     : flex-start;
  align-items         : center;
  margin              : calc(var(--space) / 2 * -1);
}

.cluster > * > * {
  margin              : calc(var(--space) / 2);
}

/* * Sidebar ---------------------------------------------------------------- */
.sidebar {
  overflow            : hidden;
}

.sidebar > * {
  display             : flex;
  flex-wrap           : wrap;
  margin              : calc(var(--s1) / 2 * -1);
}

.sidebar > * > * {
  margin              : calc(var(--s1) / 2);
  flex-grow           : 1;
}

.sidebar.-left > * > :last-child {
  flex-basis          : 0;
  flex-grow           : 999;
  min-width           : calc(50% - var(--s1));
}

.sidebar.-right > * > :first-child {
  flex-basis          : 0;
  flex-grow           : 999;
  min-width           : calc(50% - var(--s1));
}

/* * Switcher --------------------------------------------------------------- */
.switcher > * {
  display             : flex;
  flex-wrap           : wrap;
  margin              : unset calc((var(--s1) / 2) * -1);
}

.switcher > * > * {
  flex-grow           : 1;
  flex-basis          : calc((var(--measure) - (100% - var(--s1))) * 999);
  margin              : calc(var(--s1) / 2);
}

.switcher > * > :nth-last-child(n+5),
.switcher > * > :nth-last-child(n+5) ~ * {
  flex-basis          : 100%;
}
  
/* * Cover ------------------------------------------------------------------ */
.cover {
  display             : flex;
  flex-direction      : column;
  min-height          : 100vh;
  padding             : 1rem;
}

.cover > * {
  margin-top          : 1rem;
  margin-bottom       : 1rem;
}

.cover > :first-child:not(h1) {
  margin-top          : 0;
}

.cover > :last-child:not(h1) {
  margin-bottom       : 0;
}

.cover > h1 {
  display: block;
  margin-top          : auto;
  margin-bottom       : auto;
}

/* Themeing ----------------------------------------------------------------- */
@media (prefers-color-scheme: dark) {
  :root {
    --bg              : var(--c-b);
    --fg              : var(--c-w);
  }
}

[data-theme="dark"] {
  --bg                : var(--c-b);
  --fg                : var(--c-w);
}

[data-theme="light"] {
  --bg                : var(--c-w);
  --fg                : var(--c-b);
}

/* Utilities ---------------------------------------------------------------- */
/* * Background colour ------------------------------------------------------ */
