.bg\:c-5  { background-color: var(--c-5)  !important }

.bg\:c-w              { background-color: var(--c-w)  !important }
.bg\:c-g1             { background-color: var(--c-g1) !important }
.bg\:c-g2             { background-color: var(--c-g2) !important }
.bg\:c-g3             { background-color: var(--c-g3) !important }
.bg\:c-g4             { background-color: var(--c-g4) !important }
