:hover.hover\:bg\:c-4 { background-color: var(--c-4)  !important }
:hover.hover\:bg\:c-5 { background-color: var(--c-5)  !important }

:hover.hover\:bg\:c-w              { background-color: var(--c-w)  !important }
:hover.hover\:bg\:c-g1             { background-color: var(--c-g1) !important }
:hover.hover\:bg\:c-g2             { background-color: var(--c-g2) !important }
:hover.hover\:bg\:c-g3             { background-color: var(--c-g3) !important }
:hover.hover\:bg\:c-g4             { background-color: var(--c-g4) !important }
:hover.hover\:bg\:c-b              { background-color: var(--c-b)  !important }

/* * Text colour ------------------------------------------------------------ */
.text\:c-1  { color: var(--c-1) !important }
.text\:c-2  { color: var(--c-2) !important }
.text\:c-3  { color: var(--c-3) !important }
.text\:c-4  { color: var(--c-4) !important }
.text\:c-5  { color: var(--c-5) !important }

.text\:c-w  { color: var(--c-w)  !important }
.text\:c-g1 { color: var(--c-g1) !important }
.text\:c-g2 { color: var(--c-g2) !important }
.text\:c-g3 { color: var(--c-g3) !important }
.text\:c-g4 { color: var(--c-g4) !important }
.text\:c-b  { color: var(--c-b)  !important }

