:hover.hover\:text\:c-2 { color: var(--c-2) !important }
:hover.hover\:text\:c-3 { color: var(--c-3) !important }
:hover.hover\:text\:c-4 { color: var(--c-4) !important }
:hover.hover\:text\:c-5 { color: var(--c-5) !important }

:hover.hover\:text\:c-w            { color: var(--c-w)  !important }
:hover.hover\:text\:c-g1           { color: var(--c-g1) !important }
:hover.hover\:text\:c-g2           { color: var(--c-g2) !important }
:hover.hover\:text\:c-g3           { color: var(--c-g3) !important }
:hover.hover\:text\:c-g4           { color: var(--c-g4) !important }
