/* * Font size -------------------------------------------------------------- */
.font-size\:s-5           { font-size: var(--s-5) !important }
.font-size\:s-4           { font-size: var(--s-5) !important }
.font-size\:s-3           { font-size: var(--s-3) !important }
.font-size\:s-2           { font-size: var(--s-2) !important }
.font-size\:s-1           { font-size: var(--s-1) !important }
.font-size\:s0            { font-size: var(--s0)  !important }
.font-size\:s1            { font-size: var(--s1)  !important }
.font-size\:s2            { font-size: var(--s2)  !important }
.font-size\:s3            { font-size: var(--s3)  !important }
.font-size\:s4            { font-size: var(--s4)  !important }
.font-size\:s5            { font-size: var(--s5)  !important }

/* * Font family ------------------------------------------------------------ */
.font\:serif              { font-family: var(--f-serif) !important }
.font\:sans               { font-family: var(--f-sans)  !important }
.font\:mono               { font-family: var(--f-mono)  !important }

/* * Text align ------------------------------------------------------------- */
.text-align\:left         { text-align: left    !important }
.text-align\:right        { text-align: right   !important }
.text-align\:centre       { text-align: center  !important }
.text-align\:justify      { text-align: justify !important }


